.navbar {
    height: 100vh;
    padding: 1.2rem;
    background-color: rgb(248 250 252);
    border-right: 1px solid rgb(226 232 240);

    grid-column: 1/2;
    grid-row: 1/3;
}

/* SECTION PERFIL */
.logo-box{
    width: 100%;
    margin-top: 1rem;

    display: flex;
    align-items: center;
    gap: 1rem;
}
.logo-box_ico-box {
    width: 4rem;
    height: 4rem;
    border-radius: 100%;
}
.ico-box_logo{
    width: 100%;
    height: 100%;
    border-radius: 100%;
    object-fit: cover;
}
.logo-box span {
    color: var(--text-color-dark);
    font-size: 1.7rem;
    font-weight: 600;
}